/* autoprefixer grid: autoplace */

.gallery {
  @include container-desktop;
  padding-bottom: 130px;
  text-align: center;

  @media (max-width: $tablet-width) {
    @include container-tablet;
    padding-bottom: 80px;
  }

  @media (max-width: $mobile-width) {
    @include container-mobile;
    padding-bottom: 70px;
  }
}

.gallery h2 {
  @include title-desktop;
  margin-top: 0;
  margin-bottom: 21px;
}

.gallery a {
  font-family: $poppins;
  padding-bottom: 2px;
  border-bottom: 1px solid $brown;
}

.gallery__wrapper {
  @include list-style (64px 0 0, 0, none);
  display: grid;
  grid-template-columns: 260px 430px 248px 233px;
  grid-template-rows: 218px 196px;
  //justify-content: space-between;
  //align-content: space-between;
  min-height: 414px;

  @media (max-width: $tablet-width) {
    grid-template-columns: 151px 249px 134px 144px;
    grid-template-rows: 129px 110px;
    min-height: auto;
  }

  @media (max-width: $mobile-width) {
    margin-top: 50px;
    grid-template-columns: 145px 145px;
    grid-template-rows: 130px 102px 130px;
  }
}

.gallery__wrapper .gallery__item--one {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  z-index: 1;

  @media (max-width: $mobile-width) {
    grid-row: 1 / 2;
  }
}

.gallery__img--one img{
  @media (max-width: $tablet-width) {
    width: 133px;
    height: 133px;
  }

  @media (max-width: $mobile-width) {
    width: 130px;
    height: 130px;
  }
}

.gallery__wrapper .gallery__item--two {
  grid-column: 2 / 3;
  grid-row: 1 / 3;
}

.gallery__img--two img {
  @media (max-width: $tablet-width) {
    width: 231px;
    height: 240px;
  }

  @media (max-width: $mobile-width) {
    width: 145px;
    height: 226px;
  }
}

.gallery__img--three img {
  @media (max-width: $tablet-width) {
    width: 134px;
    height: 111px;
  }

  @media (max-width: $mobile-width) {
    width: 145px;
    height: 121px;
  }
}

.gallery__img--four img {
  @media (max-width: $tablet-width) {
    width: 126px;
    height: 126px;
  }
}

.gallery__img--five img {
  @media (max-width: $tablet-width) {
    width: 133px;
    height: 89px;
  }

  @media (max-width: $mobile-width) {
    width: 130px;
    height: 87px;
  }
}

.gallery__img--six img {
  @media (max-width: $tablet-width) {
    width: 126px;
    height: 111px;
  }

  @media (max-width: $mobile-width) {
    width: 130px;
    height: 115px;
  }
}

.gallery__img--seven img {
  @media (max-width: $tablet-width) {
    width: 134px;
    height: 96px;
  }
}

.gallery__wrapper .gallery__item--five {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  align-self: end;

  @media (max-width: $mobile-width) {
    order: 1;
  }
}


.gallery__wrapper .gallery__item--three {
  grid-column: 3 / 5;
  grid-row: 1 / 2;
  z-index: 1;

    @media (max-width: $mobile-width) {
      order: 3;
      grid-column: 2 / 3;
      grid-row: 3 / 4;
      align-self: end;
    }
}

.gallery__wrapper .gallery__item--six {
  grid-column: 3 / 4;
  grid-row: 2 / 3;
  align-self: end;

  @media (max-width: $mobile-width) {
    order: 2;
    grid-column: 1 / 2;
    grid-row: 3 / 4;
  }
}

.gallery__wrapper .gallery__item--four {
  grid-column: 4 / 5;
  grid-row: 1 / 2;
  justify-self: end;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.gallery__wrapper .gallery__item--seven {
  grid-column: 3 / 5;
  grid-row: 2 / 3;
  justify-self: end;
  align-self: end;
  z-index: 1;

  @media (max-width: $mobile-width) {
    display: none;
  }
}
